.login-page,
.user-creation-page,
.forgot-password-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: linear-gradient(180deg, #fafaf8, #f5f3f0);
}

/* Authentication Shared Styles */
/* Container for auth pages */
.login-container,
.user-creation-container,
.forgot-password-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

/* Form styling for auth pages */
.login-form,
.user-creation-form,
.forgot-password-form {
    background-color: white;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
    margin-bottom: 20px;
}

.login-form h1,
.user-creation-form h1,
.forgot-password-form h1 {
    text-align: center;
    color: #333;
    margin-bottom: 24px;
}

/* Form group styling */
.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: 600;
    color: #555;
}

.form-group input {
    width: 100%;
    box-sizing: border-box;
    padding: 10px 12px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
    transition: border-color 0.3s;
}

.form-group input:focus {
    border-color: #4a90e2;
    outline: none;
    box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
}

/* Primary buttons */
.login-button,
.create-account-button,
.reset-password-button {
    width: 100%;
    background-color: #4a90e2;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 12px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-top: 10px;
}

.login-button:hover,
.create-account-button:hover,
.reset-password-button:hover {
    background-color: #357abd;
}

/* Secondary buttons */
.secondary-button {
    background-color: transparent;
    color: #4a90e2;
    border: 1px solid #4a90e2;
    border-radius: 4px;
    padding: 10px 16px;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s;
    margin: 5px;
}

.secondary-button:hover {
    background-color: rgba(74, 144, 226, 0.1);
}

/* Container for multiple buttons */
.buttons-container {
    display: flex;
    justify-content: space-between;
    margin-top: 16px;
}

/* Form header with back button */
.form-header {
    position: relative;
    margin-bottom: 30px;
    padding-top: 30px;
}

.form-header h1 {
    margin-top: 15px;
    margin-bottom: 0;
}

/* Back button styling */
.back-button {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(74, 144, 226, 0.1);
    border: none;
    border-radius: 4px;
    color: #4a90e2;
    font-weight: 600;
    cursor: pointer;
    padding: 6px 10px;
    display: flex;
    align-items: center;
    font-size: 14px;
    transition: all 0.2s ease;
}

.back-button:hover {
    color: #357abd;
    background-color: rgba(74, 144, 226, 0.2);
}
